import React from 'react';
import PropTypes from 'prop-types';
import { hot } from 'react-hot-loader';
import { Typography } from '@material-ui/core';

import Hint from '../Hint';
import RadioGroup from '../RadioGroup';
import SpecialButtonGroup from '../Buttons/SpecialButtonGroup';

const buttons = [
  'Raucher',
  'Schwanger',
  'Chronische Lungenerkrankung',
  'Einnahme von Immunsuppressiva',
  'Diabetes',
  'Nierenleiden',
  'Herz-Kreislauf-Erkrankungen',
  'Asthma',
  'ber 50 Jahre alt',
];

const Step8 = ({ onChange }) => {
  return (
    <>
      <Typography variant="body2" color="primary">
        Gehrst Du einer Risikogruppe an?
      </Typography>
      <Hint
        text={`Einige Menschen haben ein hheres Risiko\n an der Lungenkrankheit COVID-19 zu\n erkranken. Wir mssen diese Personen\n besonders schtzen.`}
      />
      <SpecialButtonGroup multiSelect buttons={buttons} />
    </>
  );
};

Step8.propTypes = {};

export default hot(module)(Step8);
